import React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import "../../pages/Google-Education/google-education.css";

const BrmisAccordion = () => {
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };
  return (
    <div>
      <Accordion
        expanded={expanded === "panel1"}
        onChange={handleChange("panel1")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography className="google-accordion-heading">
            Empowered Management
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Barangay officials can quickly view reports of demographics to
                identify insightful data that helps in efficient decision
                making.
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel2"}
        onChange={handleChange("panel2")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel2bh-content"
          id="panel2bh-header"
        >
          <Typography className="google-accordion-heading">
            Efficient Documentation
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Easily generate certificates and other public documents designed
                in your own format and layout
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel3"}
        onChange={handleChange("panel3")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel3bh-content"
          id="panel3bh-header"
        >
          <Typography className="google-accordion-heading">
            Quick Brangay Transaction
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Any transaction can be easi;y searched and quickly filter or
                import complete data with names, dates and titles.
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel4"}
        onChange={handleChange("panel4")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel4bh-content"
          id="panel4bh-header"
        >
          <Typography className="google-accordion-heading">
            Business Record
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Accurate issuances and management of business registrations,
                renewals, records of payments and collection of fees
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  );
};

export default BrmisAccordion;
