import React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import "../../pages/Google-Education/google-education.css";

const GovtCareAccordion = () => {
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };
  return (
    <div>
      <Accordion
        expanded={expanded === "panel1"}
        onChange={handleChange("panel1")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography className="google-accordion-heading">
            Government IT Consultancy
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Our industry expertise can help you choose the right technology
                that works for your business.
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel2"}
        onChange={handleChange("panel2")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel2bh-content"
          id="panel2bh-header"
        >
          <Typography className="google-accordion-heading">
            Modernizing Government IT services
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Automate processes in order to speed up your agency’s services.
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel3"}
        onChange={handleChange("panel3")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel3bh-content"
          id="panel3bh-header"
        >
          <Typography className="google-accordion-heading">
            Web and Mobile Applications
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Make your services available to your customers through web and
                mobile applications.
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel4"}
        onChange={handleChange("panel4")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel4bh-content"
          id="panel4bh-header"
        >
          <Typography className="google-accordion-heading">
            IT Bootcamp Training
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Keep your employees updated with the latest technologies through
                our bootcamps.
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel5"}
        onChange={handleChange("panel5")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel5bh-content"
          id="panel4bh-header"
        >
          <Typography className="google-accordion-heading">
            Google Workspace
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Increase productivity of your team despite their location and
                still keep everything secured.
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel6"}
        onChange={handleChange("panel6")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel6bh-content"
          id="panel4bh-header"
        >
          <Typography className="google-accordion-heading">
            Cybersecurity
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Protect your agency’s data from anyone trying to steal your
                information.
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  );
};

export default GovtCareAccordion;
