import React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import "../../pages/Google-Education/google-education.css";

const EducationAccordion = () => {
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };
  return (
    <div>
      <Accordion
        expanded={expanded === "panel1"}
        onChange={handleChange("panel1")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography className="google-accordion-heading">
            Google Workspace to Bolster Social Learning
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Allow your learners to learn from each other for better learning
                impact
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel2"}
        onChange={handleChange("panel2")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel2bh-content"
          id="panel2bh-header"
        >
          <Typography className="google-accordion-heading">
            Google Workspace for Education
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>Elevate education with simple, flexible and secure tools.</li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel3"}
        onChange={handleChange("panel3")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel3bh-content"
          id="panel3bh-header"
        >
          <Typography className="google-accordion-heading">
            Implementing Learning Management Systems
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Let us help you determine the right technology for your
                institution.
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel4"}
        onChange={handleChange("panel4")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel4bh-content"
          id="panel4bh-header"
        >
          <Typography className="google-accordion-heading">
            Digital Transformation Consulting
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Discover tools that can enable your institution to be more
                digitized.
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel5"}
        onChange={handleChange("panel5")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel5bh-content"
          id="panel4bh-header"
        >
          <Typography className="google-accordion-heading">
            Google Certification Courses
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Provide an easy way for educators to adapt to the technology
                faster
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel6"}
        onChange={handleChange("panel6")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel6bh-content"
          id="panel4bh-header"
        >
          <Typography className="google-accordion-heading">
            Best Practices for Online Learning Engagements
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul style={{ listStyle: "none" }}>
              <li>
                Gain knowledge on how you can best share knowledge to your
                students effectively despite being online.
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  );
};

export default EducationAccordion;
