import React, { useState } from "react";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import Typography from "@mui/material/Typography";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import AddIcon from "@mui/icons-material/Add";
import "../../pages/Google-Education/google-education.css";
import { Link } from "react-router-dom";
import Axios from "axios";

const SupportForm = () => {
  const [sent, setsent] = useState(false);
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  const [expand, setExpand] = useState(false);

  const clicked = () => {
    if (expand == true) {
      setExpand(false);
    } else {
      setExpand(true);
    }
  };

  const [formData, setFormData] = useState({
    firstname: "",
    lastname: "",
    email: "",
    number: "",
    country: "",
    companyName: "",
    message: "",
  });

  const {
    firstname,
    lastname,
    email,
    number,
    country,
    companyName,
    message,
  } = formData;

  const onChange = async(e) => {
    setFormData((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  const formSubmit = (e) => {
    e.preventDefault();
    // const resetForm = () => {
    //   firstname = "";
    //   lastname = "";
    //   email = "";
    //   number = "";
    //   country = "";
    //   companyName = "";
    //   message = "";
    // };
    
    const data = {
      firstname,
      lastname,
      email,
      number,
      country,
      companyName, 
      message, 
    };

    Axios.post("http://localhost:5000/api/form3", data).then((res) => {
        setsent(true);
        setFormData({
          firstname: "",
          lastname: "",
          email: "",
          number: "",
          country: "",
          companyName: "",
          message: "",
        });
        setTimeout(() => {
          setsent(false);
          // resetForm();
        }, 5000);
      })
      .catch((error) => {
        console.log(error);
        console.log("message not send");
      });
  }

  return (
    <div>
      <div className="brmis-slider-form">
        <Accordion
          expanded={expanded === "panel1"}
          onChange={handleChange("panel1")}
          className="slide-form-accordion"
          onClick={clicked}
        >
          <AccordionSummary
            expandIcon={
              <AddIcon
                className={expanded ? "add-form-icon-remove" : "add-form-icon"}
              />
            }
            aria-controls="panel1bh-content"
            id="panel1bh-header"
            className="slide-form-accordion-btn"
          >
            <Typography className=" form-request-demo-heading">
              {expanded ? "< Back " : "Send Message"}
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography className="accordion-description">
              <div className="barangay__form__container">
                <form onSubmit={formSubmit}>
                  <div className="input__size__frame form__field__ml form__field__ml__1280 m__none">
                    <div className="text__field">
                      <input
                        type="text"
                        name="firstname"
                        value={firstname}
                        onChange={onChange}
                        required
                      />
                      <span></span>
                      <label htmlFor="firstname">First Name*</label>
                    </div>
                  </div>

                  <div className="input__size__frame form__field__mr__1280 m__none">
                    <div className="text__field">
                      <input
                        type="text"
                        name="lastname"
                        value={lastname}
                        onChange={onChange}
                        required
                      />
                      <span></span>
                      <label htmlFor="lastname">Last Name*</label>
                    </div>
                  </div>

                  <div className="input__size__frame form__field__mr form__field__ml__1280 form__field__mr__w1280 m__none">
                    <div className="text__field">
                      <input
                        type="email"
                        name="email"
                        value={email}
                        onChange={onChange}
                        required
                      />
                      <span></span>
                      <label htmlFor="email">Email Address*</label>
                    </div>
                  </div>

                  <div className="input__size__frame form__field__ml form__field__ml__w1280 form__field__mr__1280 m__none">
                    <div className="text__field">
                      <input
                        type="tel"
                        name="number"
                        value={number}
                        onChange={onChange}
                        required
                      />
                      <span></span>
                      <label htmlFor="number">Mobile Number*</label>
                    </div>
                  </div>

                  <div className="input__size__frame form__field__ml__1280 form__field__mr__w1280 m__none">
                    <div className="text__field">
                      <input
                        type="text"
                        name="companyName"
                        value={companyName}
                        onChange={onChange}
                        required
                      />
                      <span></span>
                      <label htmlFor="companyName">Company Name*</label>
                    </div>
                  </div>

                  <div className="input__size__frame form__field__mr m__none">
                    <div className="text__field">
                      <input
                        type="text"
                        name="country"
                        value={country}
                        onChange={onChange}
                        required
                      />
                      <span></span>
                      <label htmlFor="country">Country*</label>
                    </div>
                  </div>

                  <div
                    style={{
                      marginBottom: "2rem",
                    }}
                    className="input__size__frame__textarea m__none"
                  >
                    <div className="textarea__field">
                      <textarea
                        name="message"
                        rows="5"
                        value={message}
                        onChange={onChange}
                        required
                      ></textarea>
                      <span></span>
                      <label htmlFor="message">Message*</label>
                    </div>
                  </div>
                  <div className={sent ? "msgAppear" : "msg"}>
                    <p>Thanks for reaching out!</p>
                  </div>

                  <div className="submit-button-sec">
                    <button type="submit">
                      <div className="center-con">
                        <div className="round"></div>
                        <div id="cta">
                          <span className="arrow primera next "></span>
                        </div>
                      </div>
                      Submit
                    </button>
                  </div>
                </form>
              </div>
            </Typography>
          </AccordionDetails>
        </Accordion>
      </div>
    </div>
  );
};

export default SupportForm;
