import React, { useEffect } from "react";
import ChatBoat from "../../components/ChatBoat/ChatBoat";
import "./training.css";
import { TweenMax } from "gsap";
import $ from "jquery";
import CircleType from "circletype";

// IMPORT COMPONENTS
import NAVIGATION from "../../components/Nav-menu/Nav-menu.js";
import FOOTER from "../../components/Footer/Footer.js";

import aboutBanner from "../../assets/images/training/training-banner.png";
import circleGrid from "../../assets/images/header-circle-bg.png";

const Training = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  useEffect(() => {
    // CIRCULAR TEXT ANIMATION
    const circularType = new CircleType(document.getElementById("rotate"));
    circularType.radius(47.5);
    // CIRCULAR TEXT ANIMATION

    // CURSOR
    var cursor = $(".cursor"),
      follower = $(".cursor-follower");

    var posX = 0,
      posY = 0,
      mouseX = 0,
      mouseY = 0;

    TweenMax.to({}, 0.016, {
      repeat: -1,
      onRepeat: function() {
        posX += (mouseX - posX) / 9;
        posY += (mouseY - posY) / 9;

        TweenMax.set(follower, {
          css: {
            left: posX - 20,
            top: posY - 20,
          },
        });

        TweenMax.set(cursor, {
          css: {
            left: mouseX,
            top: mouseY,
          },
        });
      },
    });

    $(document).on("mousemove", function(e) {
      mouseX = e.pageX;
      mouseY = e.pageY;
    });

    $(".rm").on("mouseenter", function() {
      cursor.addClass("active");
      follower.addClass("active");
    });

    $(".rm").on("mouseleave", function() {
      cursor.removeClass("active");
      follower.removeClass("active");
    });
    // CURSOR
  }, []);

  return (
    <div className="training-page">
      {/* CIRCULAR TEXT */}
      <ChatBoat />
      {/* CIRCULAR TEXT */}

      {/* CURSOR */}
      <div className="cursor"></div>
      <div className="cursor-follower"></div>
      {/* CURSOR */}

      <div className="wrapper">
        {/* LINE ON BG */}
        <div className="strips">
          <div className="strips__one"></div>
          <div className="strips__two"></div>
          <div className="strips__three"></div>
          <div className="strips__four"></div>
          <div className="strips__five"></div>
          <div className="strips__six"></div>
        </div>
        {/* LINE ON BG */}

        {/* NAVIGATION */}
        <NAVIGATION />
        {/* NAVIGATION */}
      </div>

      <section id="headerSection" className="home-header-section">
        <div className="circle-grid-container">
          <img src={circleGrid} alt="" />
        </div>
        <div className="header-banner-bg"></div>
        <div className="section__container">
          <div className="header-grid-container">
            <div className="header-heading-sec">
              <div className="header-heading-container">
                <h1>Expert IT training — when and where you need it.</h1>
              </div>
            </div>
            <div className="header-banner-sec">
              <div className="header-banner-container">
                <img className="header-banner-img" src={aboutBanner} alt="" />
              </div>
            </div>
          </div>
        </div>
      </section>

      <section id="trainingOutdated">
        <div className="section__container">
          <div className="out-dated-inner-sec">
            <div className="cutomer-strory-heading-sec">
              <p>Enrich Your Workforce, Never Get Outdated.</p>
            </div>
            <div className="out-dated-des-sec">
              <p>
                Fill the gaps, bridge the knowledge to the present trends and
                stay ahead of your industry through professional upskills
                training. Enhance your workforce with skills in digital,
                analytics, and organizational transformation.
              </p>
            </div>
            <div className="study-box-container training-box-container">
              <div className="study-box box-bg1">
                <p>Needs Analysis</p>
                <span>
                  LaQuest believes that a truly effective training is one that
                  involves responds to their desired outcome.
                </span>
              </div>
              <div className="study-box box-bg2">
                <p>Tailor Fit</p>
                <span>
                  Every organization has different need and we want our programs
                  to come close to how your team will use their learning
                </span>
              </div>
              <div className="study-box box-bg3">
                <p>Experiential Learning</p>
                <span>
                  We stand by the phrase "learn by doing" and ensure to provide
                  ample amount of challenges and hands-on experience.
                </span>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section id="DeliveryFormats">
        <div className="section__container">
          <div className="delivery-formate-sec">
            <div className="stories-heading-grid">
              <div className="stories-heading-sec">
                <span>Training</span>
                <br />
                <p>Delivery Formats</p>
              </div>
            </div>
            <div className="delivery-box-sec">
              <div className="first-box-img-sec"></div>
              <div className="delivery-sec-box-sec">
                <div className="story-heading-sec">
                  <span>ON-SITE COURSE SOLUTION</span>
                  <br />
                </div>
                <div className="success-story-text-btn-sec">
                  <p>
                    We offer training sessions on-site that will allow for both
                    theoretical knowledge and active learning in order to
                    maximize retention.
                  </p>
                </div>
              </div>
              <div className="delivery-sec-box-sec delivery-middel-sec">
                <div className="story-heading-sec">
                  <span>BLENDED LEARNING</span>
                  <br />
                </div>
                <div className="success-story-text-btn-sec">
                  <p>
                    This format brings in the the best of self-paced and having
                    on-site guided activities from our experts.
                  </p>
                </div>
              </div>
              <div className="delivery-sec-box-sec">
                <div className="story-heading-sec">
                  <span>VIRTUAL TRAINING</span>
                  <br />
                </div>
                <div className="success-story-text-btn-sec">
                  <p>
                    Learn at your own pace or experience valuable and timely
                    education from anywhere.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      {/* FOOTER */}
      <FOOTER />
      {/* FOOTER */}
    </div>
  );
};

export default Training;
