import React, { useEffect, useState } from "react";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import Typography from "@mui/material/Typography";

import AddIcon from "@mui/icons-material/Add";
import "../../pages/Google-Education/google-education.css";
import { Link } from "react-router-dom";
import Axios from "axios";

const SlideForm = () => {
  const [sent, setsent] = useState(false);
  const [expanded, setExpanded] = React.useState(false);

  const [formData, setFormData] = useState({
    firstname: "",
    lastname: "",
    email: "",
    phone: "",
    barangayChairman: "",
    barangayName: "",
    barangayPopulation: "",
    barangayContactNo: "",
    barangayAddress: "",
  });

  const {
    firstname,
    lastname,
    email,
    phone,
    barangayChairman,
    barangayName,
    barangayPopulation,
    barangayContactNo,
    barangayAddress,
  } = formData;

  const onChange = (e) => {
    setFormData((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  const [expand, setExpand] = useState(false);

  const clicked = () => {
    if (expand === true) {
      setExpand(false);
    } else {
      setExpand(true);
    }
  };

  //  useEffect(() => {
  //  }, [resetForm]);

  const formSubmit = async (e) => {
    e.preventDefault();
    const data = {
      firstname,
      lastname,
      email,
      phone,
      barangayChairman,
      barangayName,
      barangayPopulation,
      barangayContactNo,
      barangayAddress,
    };

    await Axios.post("http://localhost:5000/api/form2", data)
      .then((res) => {
        setsent(true);
        setFormData({
          firstname: "",
          lastname: "",
          email: "",
          phone: "",
          barangayChairman: "",
          barangayName: "",
          barangayPopulation: "", 
          barangayContactNo: "",
          barangayAddress: "",
        });
        setTimeout(() => { 
          setsent(false);
          // resetForm();
        }, 5000);
      })
      .catch((error) => {
        console.log(error);
        console.log("message not send");
      });

    // var resetForm = () => {
    //   setFormData.firstname = "";
    //   setFormData.lastname = "";
    //   setFormData.email = "";
    //   setFormData.phone = "";
    //   setFormData.barangayChairman = "";
    //   setFormData.barangayName = "";
    //   setFormData.barangayPopulation = "";
    //   setFormData.barangayContactNo = "";
    //   setFormData.barangayAddress = "";
    // };
  };

  return (
    <div className="brmis-slider-form">
      <Accordion
        expanded={expanded === "panel1"}
        onChange={handleChange("panel1")}
        className="slide-form-accordion"
        onClick={clicked}
      >
        <AccordionSummary
          expandIcon={
            <AddIcon
              className={expanded ? "add-form-icon-remove" : "add-form-icon"}
            />
          }
          aria-controls="panel1bh-content"
          id="panel1bh-header"
          className="slide-form-accordion-btn"
        >
          <Typography className=" form-request-demo-heading">
            {expanded ? "< Back " : "Request a Demo"}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <div className="barangay__form__container">
              <form onSubmit={formSubmit}>
                <div className="input__size__frame__barangay input__frame__barangay_ml">
                  <div className="text__field">
                    <input
                      type="text"
                      name="firstname"
                      value={firstname}
                      onChange={onChange}
                      required
                    />
                    <span></span>
                    <label htmlFor="firstname">First Name</label>
                  </div>
                </div>

                <div className="input__size__frame__barangay input__frame__barangay_mr">
                  <div className="text__field">
                    <input
                      type="text"
                      name="lastname"
                      value={lastname}
                      onChange={onChange}
                      required
                    />
                    <span></span>
                    <label htmlFor="lastname">Last Name*</label>
                  </div>
                </div>

                <div className="input__size__frame__barangay input__frame__barangay_ml">
                  <div className="text__field">
                    <input
                      type="email"
                      name="email"
                      value={email}
                      onChange={onChange}
                      required
                    />
                    <span></span>
                    <label htmlFor="email">Email Address*</label>
                  </div>
                </div>

                <div className="input__size__frame__barangay input__frame__barangay_mr">
                  <div className="text__field">
                    <input
                      type="tel"
                      name="phone"
                      value={phone}
                      onChange={onChange}
                      required
                    />
                    <span></span>
                    <label htmlFor="phone">Mobile Number*</label>
                  </div>
                </div>

                <div className="input__size__frame__barangay input__frame__barangay_ml">
                  <div className="text__field">
                    <input
                      type="text"
                      name="barangayChairman"
                      value={barangayChairman}
                      onChange={onChange}
                      required
                    />
                    <span></span>
                    <label htmlFor="barangayChairman">Barangay Chairman*</label>
                  </div>
                </div>

                <div className="input__size__frame__barangay input__frame__barangay_mr">
                  <div className="text__field">
                    <input
                      type="text"
                      name="barangayName"
                      value={barangayName}
                      onChange={onChange}
                      required
                    />
                    <span></span>
                    <label htmlFor="barangayName">Barangay Name*</label>
                  </div>
                </div>

                <div className="input__size__frame__barangay input__frame__barangay_ml">
                  <div className="text__field">
                    <input
                      type="text"
                      name="barangayPopulation"
                      value={barangayPopulation}
                      onChange={onChange}
                      required
                    />
                    <span></span>
                    <label htmlFor="barangayPopulation">
                      Barangay Population*
                    </label>
                  </div>
                </div>

                <div className="input__size__frame__barangay input__frame__barangay_mr">
                  <div className="text__field">
                    <input
                      type="text"
                      name="barangayContactNo"
                      value={barangayContactNo}
                      onChange={onChange}
                      required
                    />
                    <span></span>
                    <label htmlFor="barangayContactNo">
                      Barangay Contact No.
                    </label>
                  </div>
                </div>

                <div className="input__size__frame__barangay__textarea">
                  <div className="textarea__field">
                    <textarea
                      name="barangayAddress"
                      rows="3"
                      value={barangayAddress}
                      onChange={onChange}
                      required
                    ></textarea>
                    <span></span>
                    <label htmlFor="barangayAddress">Barangay Address*</label>
                  </div>
                </div>

                <div className="eula">
                  <p className="ff__gilroy__l dark_c">
                    By providing this information; you agree that we may process
                    your personal data in accordance with our{" "}
                    <span className="ff__gilroy__e secondary_c">
                      <Link to="/EULA" className="ff__gilroy__e secondary_c">
                        End-User License Agreement (EULA).
                      </Link>
                    </span>
                  </p>
                </div>

                <div className="brmis__checkbox">
                  <input
                    type="checkbox"
                    id="brmis__agree"
                    name="brmis__agree"
                    value="Agree"
                    required
                  />
                  <p className="ff__gilroy__l dark_c">
                    Yes, I would like to receive marketing communications
                    regarding BrMIS, products, services, and events.
                  </p>
                  <div className={sent ? "msgAppear" : "msg"}>
                    <p>Thanks for reaching out!</p>
                  </div>
                </div>

                <div className="submit-button-sec">
                  <button type="submit">
                    <div className="center-con">
                      <div className="round"></div>
                      <div id="cta">
                        <span className="arrow primera next "></span>
                      </div>
                    </div>
                    Submit
                  </button>
                </div>

                {/* <div className="submit__btn">
                  <button type="submit" className="ff__gilroy_l">
                    Request Demo
                    <FontAwesomeIcon
                      className="icon__submit"
                      icon={["fas", "paper-plane"]}
                    />
                  </button>
                </div> */}
              </form>
            </div>
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  );
};

export default SlideForm;
