import React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import "../../pages/Google-Education/google-education.css";

const GoogleToolsAccordion = () => {
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };
  return (
    <div>
      <Accordion
        expanded={expanded === "panel1"}
        onChange={handleChange("panel1")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography className="google-accordion-heading">
            Google Classroom
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul className="accordion-des-ul">
              <li>Simplify Class Management</li>
              <li>Increase student engagement and original thinking</li>
              <li>Teach on secure platform</li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel2"}
        onChange={handleChange("panel2")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel2bh-content"
          id="panel2bh-header"
        >
          <Typography className="google-accordion-heading">
            Assignments
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul className="accordion-des-ul">
              <li>Quickly share classwork with students</li>
              <li>Easily access rubrics and comment bank</li>
              <li>Spot missed citations</li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel3"}
        onChange={handleChange("panel3")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel3bh-content"
          id="panel3bh-header"
        >
          <Typography className="google-accordion-heading">
            Chrome Education Upgrade
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul className="accordion-des-ul">
              <li>Control access settings</li>
              <li>27/7 Admin Support</li>
              <li>Simplify deployment for all devices</li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel4"}
        onChange={handleChange("panel4")}
        className="google-tools-accordion"
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-icon" />}
          aria-controls="panel4bh-content"
          id="panel4bh-header"
        >
          <Typography className="google-accordion-heading">
            Chromebook for Education
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography className="accordion-description">
            <ul className="accordion-des-ul">
              <li>Be in control of all of your school’s devices</li>
              <li>Versatile devices </li>
              <li>Reduce risks and increase device lifespans</li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  );
};

export default GoogleToolsAccordion;
